	module.exports = {"data":{"site":{"meta":{"title":"四条眉毛的博客","description":"I'm Lin Jin , a web developer and a travel enthusiasts. My target is to write clean and efficient code, to solve poblems on the web and to learn something more. ","author":"Lin Jin"}},"post":{"id":"/Users/fourfish/Desktop/gatsby-starter-bootstrap/src/pages/articles/Arindam Paul's Speech/index.md absPath of file >>> MarkdownRemark","html":"<h1>ScopeChains</h1>\n<html><head></head><body>\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; margin-bottom: 1.0725rem;; max-width: 750px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 57.51412429378531%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"&#x7B2C;&#x4E00;&#x9898;\" title=\"\" src=\"/static/89b350957e8f4e340df808d096d73676-245fd.png\" srcset=\"/static/89b350957e8f4e340df808d096d73676-7aee9.png 188w,\n/static/89b350957e8f4e340df808d096d73676-2014b.png 375w,\n/static/89b350957e8f4e340df808d096d73676-245fd.png 750w,\n/static/89b350957e8f4e340df808d096d73676-116ee.png 885w\" sizes=\"(max-width: 750px) 100vw, 750px\">\n    </span>\n  </span>\n  \n<figcaption>&#x7B2C;&#x4E00;&#x9898;</figcaption></body></html>\n<p>图片描述的是 myG()执行后，执行环境中还保留的作用域链。因为闭包的缘故，只有 g()的作用域被回收了。</p>\n<p>当时看到结果时，对 g()的作用域被回收这个现象还是有疑惑的。因为，我心中的闭包是函数和其绑定的周边状态（词法环境）的组合。而对“周边状态”的范围，我没有清楚的界限。我认为 g 函数中的变量 e 会被保留。</p>\n<p>后来改了一下代码：</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">function</span> <span class=\"token function\">g</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n\t<span class=\"token keyword\">var</span> e <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span>\n\te<span class=\"token operator\">++</span><span class=\"token punctuation\">;</span>\n\t<span class=\"token keyword\">return</span> e<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>运行了下代码，发现 e 的值并不会像 d 一样会叠加。这才发现自己错了。</p>\n<p>事后，又想了想，认为 g 的作用域肯定是要被回收的。毕竟有没有人引用它的作用域。</p>\n<hr>\n<h1>EventLoop</h1>\n<p>对事件循环的讲解，我并没有疑问。只是第一次看到有人把 Js 执行流程用动画表现出来，感觉很赏心悦目。</p>\n<html><head></head><body>\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; margin-bottom: 1.0725rem;; max-width: 620px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 72.09677419354838%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"EventLoop\" title=\"\" src=\"/static/0d29efdbaae8cc1a85cdcbb9a90c3d24-3bb03.png\" srcset=\"/static/0d29efdbaae8cc1a85cdcbb9a90c3d24-fc894.png 188w,\n/static/0d29efdbaae8cc1a85cdcbb9a90c3d24-d0f89.png 375w,\n/static/0d29efdbaae8cc1a85cdcbb9a90c3d24-3bb03.png 620w\" sizes=\"(max-width: 620px) 100vw, 620px\">\n    </span>\n  </span>\n  \n<figcaption>EventLoop</figcaption></body></html>\n<hr>\n<h1>Vedio</h1>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <html><head></head><body><iframe src=\"https://www.youtube.com/embed/QyUFheng6J0\" frameborder=\"0\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe></body></html>\n          </div>\n          </div>","frontmatter":{"layout":"post","title":"《Arindam Paul - JavaScript VM internals, EventLoop, Async and ScopeChains》观后感","path":"/Arindam Paul's Speech/","categories":["JavaScript"],"date":"2017/10/10"}}},"pathContext":{"path":"/Arindam Paul's Speech/"}}

